<template lang="pug">
grid-item(col-end=2, col-end-sm=1)
  v-select(
    v-model="value_",
    :label="label",
    :name="name",
    :items="districtsSelect",
    dense,
    outlined,
    dark
  )
</template>

<script>
import mixin from "../../mixin";

export default {
  props: {
    fieldId: {
      type: Number,
      required: true,
    },
  },
  data: () => ({
    value_: null,
  }),
  computed: {
    moduleId() {
      return this.$rest("modules").item.id;
    },
    itemId() {
      return this.$route.params.sub;
    },
    districts() {
      return this.$rest("districts").list;
    },
    districtsSelect() {
      const districts = this.districts.map(({ title, id }) => ({
        text: title,
        value: id,
      }));

      districts.unshift({
        text: "",
        value: "",
      });

      return districts;
    },
  },
  watch: {
    value(v) {
      this.value_ = v;
    },
  },
  created() {
    this.$rest("districts").get({
      params: {
        moduleId: this.moduleId,
        fields: "id,title",
      },
      keepCache: true,
    });
  },
  mounted() {
    this.value_ = this.value;
  },
  mixins: [mixin],
};
</script>
