export default async ({ component, value, moduleId }) => {
  let type = component.$rest("types").list.find(({ id }) => id == value);

  if (type) return Promise.resolve(type.title);

  return component
    .$rest("types")
    .get({
      params: {
        moduleId
      },
      keepCache: true
    })
    .then(r => {
      const type = r.find(({ id }) => id == value);
      return type ? type.title : "";
    });
};
