export default async ({ component, value, moduleId }) => {
  console.log("ok");
  let district = component
    .$rest("districts")
    .list.find(({ id }) => id == value);
  if (district) return Promise.resolve(district.title);

  return component
    .$rest("districts")
    .get({
      params: {
        moduleId
      },
      keepCache: true
    })
    .then(r => {
      const district = r.find(({ id }) => id == value);
      return district ? district.title : "";
    });
};
